'use client';

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { useAuth } from '@/context/AuthContext'
import { getUserBookings, services } from '@/services/api'
import styles from './dashboard.module.css'


type Booking = {
  id: number
  serviceId: number
  dateTime: string
}

export default function DashboardPage() {
  const { user, logout } = useAuth()
  const router = useRouter()
  const [loading, setLoading] = useState(true)
  const [bookings, setBookings] = useState<Booking[]>([])

  useEffect(() => {
    if (!user) {
      router.push('/login')
    } else {
      const userBookings = getUserBookings(user)
      setBookings(userBookings)
      setLoading(false)
    }
  }, [user])

  if (loading) return <p>Lade deine Buchungen...</p>

  return (
    <div className={styles.container}>
      <h1>Willkommen zurÃ¼ck, {user} ðŸ‘‹</h1>

      <button onClick={logout} className={styles.logoutBtn}>
        Logout
      </button>

      <h2>Deine Buchungen</h2>
      {bookings.length === 0 ? (
        <p>Du hast noch keine Termine gebucht.</p>
      ) : (
        <ul className={styles.bookingList}>
          {bookings.map((b) => {
            const service = services.find((s) => s.id === b.serviceId)
            return (
              <li key={b.id} className={styles.bookingItem}>
                <strong>{service?.name}</strong> <br />
                ðŸ“… {new Date(b.dateTime).toLocaleString('de-DE')}
              </li>
            )
          })}
        </ul>
      )}
    </div>
  )
}
